//colors
$color_alabaster_approx: #fbfbfb;
$red: red;
$white: #fff;
$color_celeste_approx: #ccc;

.search {
    .form-horizontal {
        display: inline-block;
    }
}

.place-div {
	label {
		font-weight: 400;
		font-size: 1.4rem;
	}
	input[type=checkbox] {
		margin-right: .3rem;
	}
	.checkbtn {
		background-color: $color_alabaster_approx;
		text-align: right;
		img {
			height: 10px;
			margin-left: 3px;
		}
		.ri {
			border-right: none;
		}
		a {
			height: 30px;
			line-height: 30px;
			display: inline-block;
			width: 60px;
			text-align: center;
		}
	}
	.smallplace {
		.ratio {
			color: $red;
		}
		label {
			padding-right: 10px;
			text-align: left;
			width: auto;
			float: left;
			cursor: pointer;
		}
		.citys {
			width: auto;
			background-color: $white;
			position: absolute;
			top: 35px;
			border: 1px solid $color_celeste_approx;
			z-index: 100;
			visibility: hidden;
			.row-div {
				min-width: 250px;
				padding: 10px;
				//Instead of the line below you could use @include box-sizing($bs)
				box-sizing: border-box;
				label span {
					max-width: 175px;
					white-space: nowrap;
					vertical-align: middle;
					font-size: 1.4rem;
				}
			}
			> i.jt {
				width: 0;
				height: 0;
				border-left: 8px solid transparent;
				border-right: 8px solid transparent;
				border-bottom: 10px solid $color_celeste_approx;
				position: absolute;
				top: -10px;
				left: 20px;
				i {
					width: 0;
					height: 0;
					border-left: 8px solid transparent;
					border-right: 8px solid transparent;
					border-bottom: 10px solid $white;
					position: absolute;
					top: 2px;
					left: -8px;
				}
			}
		}
		p {
			float: left;
			width: auto;
			margin: 2px 0;
		}
		> div {
			float: left;
			width: 170px;
			margin: 0;
			padding-bottom: 10px;
            padding-top: 5px;
            position: relative;
		}
		.place-tooltips:hover .citys {
			visibility: visible;
		}
	}
}
.show-place-div {
	margin-left: 85px;
	font-size: 15px;
	.smallplace {
		label {
			min-width: 105px;
			width: auto;
		}
		input {
			margin-left: 0;
		}
		.citys .row-div p {
			margin: 5px 0 10px;
		}
	}
}

.clearfloat {
    zoom: 1; 
    &:after {
        display: block;
        clear: both;
        content: "";
        visibility: hidden;
        height: 0; 
    }
}

.tree-item {
	text-align: left;
}

.product-box {
    .input-group {
        input[type="text"],
        input[type="number"], {
            min-width: 0;
            width: 100px;
        }
    }
    input[type="text"],
    input[type="number"], {
        min-width: 0;
        width: 100px;
    }
}



.form-table {
    .input-group {
        .add-box {
            display: inline-block;
            input {
                min-width: 0;
                width: 100px;
            }
            .btn {
                margin: 0;
            }
        }
    }
    .attr-box {
        .input-group {
            input {
                min-width: 0;
            }
        }
        .check-label {
            margin-right: 8px;
            position: relative;
            label {
                padding-right: 0;
            }
            input {
                width: 60px;
                text-align: center;
                background: rgba($color: #fff, $alpha: .3);
                border: none;
                border-bottom: 1px solid;
                color: #fff;
            }
            .fa-remove {
                position: absolute;
                display: block;
                cursor: pointer;
                text-align: center;
                background: #ababab;
                border-radius: 50%;
                width: 20px;
                height: 20px;
                line-height: 20px;
                top: -9px;
                right: -9px;
            }
            &.attr-block {
                .fa-remove {
                    top: 0;
                    right: 0px;
                    width: 30px;
                    height: 30px;
                    line-height: 30px;
                }
            }
        }
    }
}


.attr-block {
    display: block;
}

.product-table,
.regional-table {
    border: 1px solid #ddd;
    border-collapse: collapse;
    td,
    th {
        border-left: 1px solid #ddd;
        border-top: 1px solid #ddd;
    }
}

.regional-table {
    input {
        display: block;
        width: 100%;
        padding: 6px 5px;
        line-height: 1.42857;
        transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
        border: 0;
        border-bottom: 1px solid #c2cad8;
        color: #555;
        box-shadow: none;
        font-size: 14px;
    }
}

.toggle-box {
	cursor: pointer;
	.fa {
		&::before {
			content: "\f00d";
		}
	}
	&.active {
		color: #f00;
		.fa {
			&::before {
				content: "\f00c";
			}
		}
	}
}

.progress-bar {
	width: 80%;
    height: 50px;
    position: relative;
	margin: 30px 0 0 100px;
	div {
		width: 100%;
		height: 10px;
		position: absolute;
		top:50%;
		left: 0;
		margin-top:-20px;
		border:1px solid #ddd;
		background: #ccc;
		span {
			position: absolute;
			display: inline-block;
			background: green;
			height: 10px;
			width: 25%;
		}
	}
	>span {
		position: absolute;
		top:0;
		margin-top: -10px;
		width: 40px;
		height: 40px;
		border:2px solid #ddd;
		border-radius: 50%;
		background:#ccc;
		margin-left: -20px;
		color:#333;
		font-size: 12px;
		line-height: 35px;
		i {
			color: #000;
			font-style: normal;
			width: 120px;
			position: absolute;
			left: -40px;
		}
		&:nth-child(1){
			left: 0%;
		}
		&:nth-child(2){
			left: 0%;
		}
		&:nth-child(3){
			left: 25%;
		}
		&:nth-child(4){
			left: 50%;
		}
		&:nth-child(5){
			left: 75%;
		}
		&:nth-child(6){
			left: 100%;
		}
		&.active {
			background:green;
			color: #fff;
		}
	}
}

.pull-right {
	float: right;
}

.panel {
	margin-top: 20px;
	.panel-header {
		line-height: 40px;
		background-color: #fff;
		padding: 0 10px;
	}
	.panel-body {
		padding: 10px;
	}
}

.text-right {
	text-align: right;
}

.table-left {
	td,
	th {
		text-align: left;
	}
}

.remark-box {
	textarea {
		width: 100%;
		min-height: 100px;
	}
}

.multi-image-box {
	border: 1px dotted;
	padding: 5px;
	.image-item {
		display: inline-block;
		position: relative;
		background-color: #fff;
		.fa {
			position: absolute;
			top: 0;
			right: 0;
			background: rgba(#333, .2);
			border-radius: 50%;
			display: block;
			width: 20px;
			height: 20px;
			text-align: center;
			line-height: 20px;
		}
		img {
			width: 120px;
			height: 120px;
		}
		&:hover {
			border: 1px solid rgb(240, 143, 143);
		}
	}
	.add-item {
		display: inline-block;
		width: 120px;
		height: 120px;
		border: 1px solid rgb(106, 241, 101);
		text-align: center;
		font-size: 80px;
		vertical-align: top;
	}
}

table {
	.goods-info {
		text-align: left;
		.thumb {
			float: left;
		}
	}
}